@import '../../scss/_utils.scss';
@import '../../scss/_animations.scss';

.home{
    position: relative;
    background: transparent;

    &::-webkit-scrollbar {
        display: none;
    }

    .bg-2{
        // height: 4000px;
        background-image: url(../../../public/images/bg2-custom.png);
        background-position: top center;
        background-size: cover;
        background-repeat: no-repeat;
        // transform: translateY(-8px);
        padding-bottom: 10px;
    }

    .collect{
        margin-top: 100px;

        .title-content{
            padding-top: 30px;

            span{
                text-align: center;
                width: 100%;
                display: block;
                font-size: 18px;
            }
        }

        .paragraph .col-xl-12{
            text-align: center;
            line-height: 40px;
        }
    }

    .bg-3{
        // transform: translateY(-400px);
        background-image: url(../../../public/images/bg3.jpg);
        // height: 1300px;
        background-position: top center;
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;
        padding-top: 70px;

    }

    .bg-4{
        // transform: translateY(-400px);
        background-image: url(../../../public/images/bg4.jpg);
        // height: 7300px;
        background-position: top center;
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;
        padding-top: 70px;
        padding-bottom: 100px;

        .tokenomics-link-bg{
           position: absolute;
            top: -170px;
            left: 0;
            width: 100%;
            height: 300px;

            img{
                height: 100%;
                height: 100%;
            }
        }

        .member{
            margin-top: 20px;
            transition: all .5s ease-in-out;

            &:hover{
                cursor: pointer;
                transform: translateY(-10px);

                img{
                    filter: drop-shadow(0 0 10px #E9C124);
                }
            }

            img{
                width: 100%;
                height: auto;
            }
        }

    }

    .infor-subscribe{
        position: absolute;
        bottom: 1.5%;
        right: 5%;
        color: #fff;
        z-index: 3;

        &-title{
            text-align: center;
            font-style: italic;
            font-size: 50px;
            font-weight: 800;
            background-image: linear-gradient(180deg, #FDE426 10%, #C07320 90%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }

        &-input{
            display: flex;
            align-items: center;
            background-color: rgba(0, 0, 0, 0.539);
            width: 440px;

            input{
                background-color: transparent;
                border: none;
                outline: none;
                flex: 1;
                height: 45px;
                padding-left: 20px;
                color: #B5D7DB;
                font-weight: 500;
            }

            &-icon{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 70px;
                height: 45px;
                border-top-left-radius: 20px;
                border-bottom-left-radius: 20px;
                background-image: linear-gradient(90deg, #7BB7BE 10%, #A0CBD1 90%);
                transition: all 0.5s ease-in-out;

                i{
                    transition: all 0.5s ease-in-out;
                }

                &:hover{
                    width: 90px;
                    cursor: pointer;
                    i{
                        color: #8562E5;
                        transform: scale(1.3);
                    }
                }
            }

        }
        &-social{
            display: flex;
            justify-content: space-between;
            margin-top: 20px;
            padding: 0 30px;

            &-item{
                min-width: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 12px;
                background-image: linear-gradient(180deg, #e1cb21 10%, #C07320 90%);
                border-radius: 6px;
                transition: all 0.5s ease-in-out;

                i{
                    transition: all 0.5s ease-in-out;
                }
            }

            &-item:hover{
                cursor: pointer;
                transform: scale(1.2) translateY(-10px);
                filter: drop-shadow(0 0 4px #E9C124);

                i{
                    color: rgb(62, 62, 62);
                    transform: rotate(360deg);
                }
            }
        }
    }
}


