.intro{
    // display: flex;
    height: 100vh;
    width:100vw;
    display: flex;
    flex-direction: column;
    align-items: center;

    &-content{
        flex: 1;
    }
    
    &-title-img{
        height: 18%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 12%;

        img{
            height: 100%;
            width: auto;
        }
    }

    &-title-wrap{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &-title-slogan{
        width: auto;
        text-transform: uppercase;
        text-align: center;
        font-size: 24px;
        font-weight: 900;
        color: #fff !important;
        font-style: italic;
        padding: 8px 14px;
        margin: 0 20px;
        // border: 1px solid rgba(0, 0, 0, 0.302);
        // border-radius: 12px;
        // background-color: rgba(0, 0, 0, 0.305);
        opacity: 0;
        text-shadow: 2px 0 0 #000, -2px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000;
    }

    &-title-buttons{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 5%;

        &-marketplace{
            transform: rotate(-2deg);
            margin-right: 12px;
            background-image: url(../../../public/images/bg-marketplace-btn.png);
        }

        &-buytoken{
            transform: rotate(2deg);
            margin-left: 12px;
            background-image: url(../../../public/images/bg-buytoken-btn.png);
        }

        &-marketplace,
        &-buytoken{
            // height: 71px;
            min-width: 220px;
            color: #F5EA6B;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 22px;
            font-weight: 900;
            padding: 20px 30px;
            background-position: center center;
            background-size: contain;
            background-repeat: no-repeat;
            transition: all 0.3s ease-in-out;

            &:hover{
                transform: scale(0.9);
                cursor: pointer;
            }
        }
    }

    // &-skin{
    //     height: 250px;
    //     display: flex;
    //     justify-content: center;
    //     align-items: flex-end;
    //     margin-top: 100px;
    //     img{
    //         width: 65%;
    //         height: auto;
    //     }
    // }
}