    .game-play{
        background-image: url(../../../public/images/bg2.jpg);
        height: 1000px;
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;
        padding-top: 100px;

        .link-bg{
            position: absolute;
            top: -154px;
            left: 0;
            width: 100%;
        }

        .blur-connect{
            position: absolute;
            top: -100px;
            left: 0;
            width: 100%;
            z-index: 2;
        }

        &-video{
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;

            &-img{
                width: 800px;

                img{
                    width: 100%;
                    height: 100%;
                }
            }

            &-btn{
                position: absolute;
                top: 50%;
                left: 50%;
                height: 90px;
                transform: translate(-50%,-50%);
                transition: all 0.5s ease-in-out;

                img{
                    height: 100%;
                    width: auto;
                }

                &:hover{
                    cursor: pointer;
                    transform: translate(-50%,-50%) scale(1.1);

                    img{
                        filter: drop-shadow(0 0 20px #5C2791);
                    }
                }
            }
        }

        
    }