
@media (min-width: 1000px) and (max-width: 1444px) {
    .mt-90{
        margin-top: 40px !important;
    }
    .background-intro {
        .skin{
            width: 100%;
            bottom: 14%;
            
            .intro-skin{
                height: 300px;

                &--1{
                    margin-right: 15% !important;
                }
                &--2{
                    margin-left: 15% !important;
                }
            }
        }
    }
}


/* PC only */
@media (min-width: 1024px) {
    .hide-on-pc {
        display: none !important;
    }

}

/* Tablet to PC low resolution */

@media (min-width: 985px) and (max-width: 1200px) {
    .intro-skin{
        margin-top: 16px;
    }
}


/* Tablet Only */

@media (min-width: 740px) and (max-width: 1023px) {
    .hidden-tl{
        display: none !important;
    }

    
}

/* Mobile & Tablet */
@media (max-width: 1023px) {
    .hidden-t {
        display: none;
    }

    .hidden-m-t {
        display: none !important;
    }
}

/* Mobile Only */
@media (max-width: 739px) {
    .hidden-m {
        display: none !important;
    }
    .App{
        .super-slider-img{
            width: 150px !important;
            height: 200px !important;
    
            img{
                height: auto !important;
            }
        }

        .paragraph{
            font-size: 16px !important;
            line-height: 30px;

            .col-xl-12{
                padding: 0 20px;
            }
        }
    
        .home {
            .intro-title-img{
                width: 100%;
                margin-top: 93px;

                img{
                    width: 100%;
                    height: auto;
                }
            }

            .intro-title-buttons{
                margin-top: 50px;
            }
    
            .intro-title-buttons-marketplace, 
            .intro-title-buttons-buytoken{
                font-size: 16px;
                min-width: 100px;
            }

            .buy .link-bg {
                top: -31px;
            }

            .buy-paragraph{
                padding-top: 190px;
            }

            .buy-paragraph .col-xl-12{
                padding: 0 20px;
            }

            .buy-form{
                height: 200px;
            }

            .buy-form-tree{
                left: -9%;
            }

            .buy-form-tree img{
                height: 150px;
            }

            .buy-form-balance{
                font-size: 12px;
                padding: 38px 26px;
                min-width: 215px;
            }

            .buy-form-buy-btn{
                height: unset;
                width: 137px;
                font-size: 14px;
                bottom: 0;
            }

            .buy-form-content{
                width: 280px;
                left: 70%;
                margin-top: 4px;
            }

            .buy-form-content-row{
                align-items: center;
                width: 80%;
                margin-left: 0;
            }
            .buy-form-content-title{
                font-size: 10px;
                min-width: unset;
                display: flex;
                align-items: center;
            }

            .buy-form-content-title--first{
                min-width: 58px;
            }

            .buy-form-content-input {
                height: 30px;
                width: 100px;
                font-size: 13px;
            }

            .about-game{
                .title-content{
                    font-size: 20px;
                    padding-top: 33px;
                }

                .about-game-book{
                    height: 280px;
                }
            }

            .collect {
                margin-top: 20px;
            }

            .collect .title-content {
                padding-top: 24px;
                font-size: 17px;
            }

            .collect .title-content span{
                font-size: 14px;
            }

            .game-play{
                padding-top: 70px;
                height: unset;
                padding-bottom: 100px;

                .title{
                    padding-top: 40px;
                }

                .game-play-video{
                    width: 100%;

                    &-bg{
                        width: 98%;
                    }

                    &-btn{
                        height: 60px;
                    }
                }

            }
            .bg-3{
                padding-top: 0;
            }

            .tokenomics .title {
                transform: translateY(99px);
            }

            .tokenomics .title .title-content{
                font-size: 20px;
                padding-top: 32px;
            }

            .tokenomics-link-bg{
                top: -13px;
            }

            .tokenomics-table-content{
                width: 95%;
            }

            .road-map{
                margin-top: 50px;

                .title{
                    font-size: 26px;
                }

                .road-map-boss img{
                    width: 300px;
                }
            }

            .road-map-land{
                width: 130px;
            }

            .road-map-land-title{
                height: 27px;
            }

            .road-map-bridge--1 {
                margin-top: 145px;
                img{
                    transform: scale(2.7) rotate(-14deg);
                }
            }

            .road-map-bridge--2 {
                margin-top: 95px;
                img{
                    transform: scale(2.9) rotate(1deg);
                }
            }

            .road-map-bridge--3 {
                margin-top: 60px;
                img{
                    transform: scale(2.2) rotate(-12deg);
                }
            }

            .road-map-bridge--4 {
                margin-top: 69px;
                margin-left: 15px;

                img{
                    transform: scale(2.7) rotate(-9deg);
                }
            }

            .road-map{
                padding-bottom: 40px;
            }

            .road-map .final-boss{
                height: 100px;
                transform: translate(36px,-92px);
                animation: final-boss-up-and-down 1s infinite alternate;
                // animation-delay: .4s;
            
            }

            .bg-4{
                padding-top: 0;

                .tokenomics-link-bg{
                    top: -39px;
                    height: 71px;
                }

                .core-members{
                    margin-top: 150px;
                }

                .core-members .title-content{
                    font-size: 17px;
                    padding-top: 35px;
                }
            }

            .infor-subscribe{
                width: 98%;
                right: 1%;
                bottom: 40vh;
            }

            .infor-subscribe-input{
                width: 100%;
            }

            .bg-4 .partner-title{
                font-size: 40px;
            }

            .home .bg-4 .partner-logo {
                margin-top: 31px;
            }

            .bg-4{
                padding-bottom: 20px;
            }
        }

        .road-map-modal-bg{
            width: 98% !important;
            height: 360px;
        }

        .road-map-modal-bg img {
            width: 100%;
            height: 100% !important;
        }

        .home .road-map-modal-close-btn{
            height: 60px !important;
            width: 60px !important;
            right: 10px !important;
        }
        .home .road-map-modal-content-item .icon{
            width: 20px;
        }

        .home .road-map-modal-content-item .titles{
            font-size: 16px;
        }

        .super-slider{
            padding: 80px 0 50px;
        }

        .title{

            &-bg{
                height: 150px;
            }

            &-content{
                padding-top: 26px;
            }
        }

        .video-wrap{
            width: 90%;
            &-border{

                img{
                    height: 353px;
                    width: 100%;
                }
            }
        }

        .modal__bg iframe{
            height: 300px;
            width: 92%;
            border-radius:8px;
        }
        

        .intro-title-buttons {
            margin-top: 10px !important;
        }

        .mt-90{
            margin-top: 40px !important;
        }

        .buy-form-content-row.first{
            margin-bottom: 5px;
        }

        .buy-form-content-error-message{
            font-size: 10px;
            bottom: 24px;
            width: 100%;
        }

        .background-intro {
            .skin{
                width: 100%;
                bottom: 14%;
                
                .intro-skin{
                    height: 180px;

                    &--1{
                        margin-right: 10px !important;
                    }
                    &--2{
                        margin-left: 10px !important;
                    }
                }
            }
        }

        .super-slider-2{
            padding: 31% 0;
            bottom: -8%;
            right: -13%;

            .super-slider-2-img{
                width: 30vh;
            }
        }
    }
}

/* 500*/
@media (min-width: 500px) and (max-width: 742px) {
    .hidden-500plus {
        display: none !important;
    }
}


@media (max-width: 500px) {
    .hidden-500{
        display: none !important;
    }
}

// Motherfucking Iphone 5S
@media (max-width: 375px) {
    .main-content{
        width: 100% !important;
    }
}
