@import '../../scss/_animations.scss';

// fonts
@import url('https://fonts.googleapis.com/css2?family=Chewy&display=swap');

.super-slider-2{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    transition: all 0.2s linear;
    transform: translate(0px, 25%);
    padding: 24% 0;
    overflow: hidden;
    z-index: 10;

    .header{
        display: flex;
        margin-bottom: 0;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
        -ms-flex-pack: center;
        justify-content: center;
        font-family: Chewy,sans-serif;
        color: #25e8d8;
        font-size: 90px;
        line-height: 100px;
        text-align: center;
        letter-spacing: 1.27px;
        letter-spacing: 20px;
        text-shadow: 1px 1px 5px rgb(0 0 0 / 50%);
        font-weight: 900;
    }

    &-content{
        display: flex;
        width: 100%;
        overflow: auto;
        transition: all 0.5s linear;
    }

    &-content-wrap{
        display: flex;
        will-change: transform;
        transition: all .3s linear;
        
        transform: translateY(0px) translateZ(0) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
    }

    &-content::-webkit-scrollbar {
        display: none;
    }

    &-img{
        margin: 10px;
        border-radius: 10px;
        // height: 300px;
        width: 20vw;
        overflow: hidden;
        display: flex;
        justify-content: center;
        transition: all 0.5s ease-in-out;

        img{
            width: 100%;
            height: auto;
        }

        &:hover{
            cursor: pointer;
            // animation: skew-cart .5s;
            transform: scale(0.98);
        }
    }
}