@keyframes swing {
    0% { transform: rotate(3deg); }
    100% { transform: rotate(-3deg); }
}

@keyframes glowLight {
  0%   {
	box-shadow: rgba(168, 176, 55, 0.451) 10px 40px 29px
  }
  100% {
	box-shadow: rgb(174, 176, 55) 10px 40px 40px
  }
}

@keyframes ball-breathing{
    0% {
        box-shadow:
            0 0 5px 5px #fff,
            0 0 25px 5px rgba(218, 239, 57, 0.558),
            0 0 45px 20px rgba(199, 212, 113, 0.434);
    }

    100%{
        box-shadow:
            0 0 10px 10px #fff,
            0 0 50px 30px rgba(218, 239, 57, 0.558),
            0 0 80px 55px rgba(199, 212, 113, 0.434);
    }
}

@keyframes outline-btn-play-breathing{
    0% {
        box-shadow:
            0 0 5px 5px #fff,
            0 0 15px 10px rgba(218, 239, 57, 0.558),
            0 0 25px 20px rgba(199, 212, 113, 0.434);
    }

    100%{
        box-shadow:
            0 0 10px 5px #fff,
            0 0 20px 10px rgba(218, 239, 57, 0.558),
            0 0 30px 35px rgba(199, 212, 113, 0.434);
    }
}

@keyframes btn-breathing {
  0% { transform: scale(1); }
    100% { transform: scale(0.98); }
}

@keyframes book-up-and-down {
  0% { 
    transform: translateY(10px) scale(0.97);
  }
  
  100% { 
    transform: translateY(0) scale(1);
  }
}

@keyframes skew-cart {
  0% { 
    transform: skew(0deg, 0deg) translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) ;
  }

  25% { 
    transform: skew(-9deg, -9deg) translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) ;
  }

  50% { 
    transform: skew(0deg, 0deg) translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) ;
  }

  75% { 
    transform: skew(9deg, 9deg) translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) ;
  }
  
  100% { 
    transform: skew(0deg, 0deg) translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) ;
  }
}

@keyframes land-up-and-down {
  0% { 
    transform: translateY(10px);
  }
  
  100% { 
    transform: translateY(0);
  }
}

@keyframes final-boss-up-and-down {
  0% { 
    transform: translate(36px,-102px);
  }
  
  100% { 
    transform: translate(36px,-92px);
  }
}

@keyframes butterfly-move {
  0% { 
      right: -16px;
      top: -61px;
  }
  5% { 
      right: -16px;
      top: -61px;
  }
  10% { 
      right: -16px;
      top: -61px;
      transform: rotateX(180deg)
  }
  100% { 
    right: -134px;
    top: 328px;
  }
}

@keyframes tail {
    0% {
    width: 0;
    }

    30% {
    width: 100px;
    }

    100% {
    width: 0;
    }
}

@keyframes shining {
  0% {
    width: 0;
  }
  
  50% {
    width: 30px;
  }
  
  100% {
    width: 0;
  }
}

@keyframes shooting {
  0% {
    transform: translateX(0);
  }
  
  100% {
    transform: translateX(300px);
  }
}

@keyframes sky {
  0% {
    transform: rotate(45deg);
  }
  
  100% {
    transform: rotate(45 + 360deg);
  }
}

@keyframes planet-rotate {
  0% { 
    transform: rotate(0deg);
  }
  
  100% { 
    transform: rotate(360deg);
  }
}

@keyframes planet-rotate-loading {
  0% { 
    transform: rotate(0deg) translate(-50%,-50%);
  }
  
  100% { 
    transform: rotate(360deg) translate(-50%,-50%);
  }
}

@keyframes planet-light-1 {
  0% { 
    filter: drop-shadow(0 0 12px #E9C124);
  }
  
  100% { 
    filter: drop-shadow(0 0 20px #E9C124);
  }
}

@keyframes planet-light-2 {
  0% { 
    filter: drop-shadow(0 0 12px #D87738);
  }
  
  100% { 
    filter: drop-shadow(0 0 40px #D87738);
  }
}

@keyframes planet-light-4 {
  0% { 
    filter: drop-shadow(0 0 12px #1A69B5);
  }
  
  100% { 
    filter: drop-shadow(0 0 40px #1A69B5);
  }
}

@keyframes start-breathing{
    0% {
        box-shadow:
            0 0 3px 1px #74F8FD,
            0 0 10px 5px #3397D5,
            0 0 15px 10px #0A5BB7;
    }

    100%{
        box-shadow:
            0 0 2px 3px #74F8FD,
            0 0 15px 10px #3397D5,
            0 0 25px 15px #4086d6;
    }
}

@keyframes book-out {
  0% {
    transform: rotate(0deg) scale(1);
    opacity: 1;
  }
  
  100% {
    opacity: 0;
    transform: rotate(360deg) scale(0);
  }
}

@keyframes video-frame-open {
  0% {
    transform: translate(-50%,-50%) scale(0) ;
    opacity: 0;
  }
  
  100% {
    opacity: 1;
    transform: translate(-50%,-50%) scale(1) ;
  }
}

@keyframes modal-road-map-open {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes load {
    0% {
        left: -100px;
    }
    100% {
        left: 220px;
    }
}

@keyframes txt {
    0% {
        content: "LOADING";
    }
    35% {
        content: "LOADING.";
    }
    65% {
        content: "LOADING..";
    }
    100% {
        content: "LOADING...";
    }
}

@keyframes shadow {
    0%{
        width: 80px;
    }
    50% {
        width: 20px;
    }
    100% {
        width: 80px;
    }
}

@keyframes logo-loading-screen {
    0%{
        transform: translateY(-20px);
    }
    50% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-20px);
    }
}