   @import '../../scss/_animations.scss';
   
   .about-game{
        margin: 80px 0;

        &-icon{
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            transition: all 0.5s linear;
            padding: 50px 20px;

            animation: book-up-and-down 1s infinite alternate;

            &-wrap{
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                position: relative;
                transition: all 0.5s linear;

                &:hover{
                cursor: pointer;

                img{
                        -webkit-filter: drop-shadow(5px 5px 5px rgb(237, 241, 44));
                    filter: drop-shadow(5px 5px 5px rgb(237, 241, 44)); 
                    }
                }
            }

            &-wrap.out .about-game-book{
                animation: book-out 1s forwards;
            } 

        }

        &-book{
            height: 380px;
            position: relative;
            // animation: book-out 1s;
            // animation-direction: reverse;

            img{
                height: 100%;
                width: auto;

                -webkit-filter: drop-shadow(5px 5px 5px #222);
                filter: drop-shadow(5px 5px 5px #222); 
            }

            
        }

        &-book::after{
            content: '';
            position: absolute;
            top: 24%;
            left: 30%;
            transform: translate(-50%,-50%);
            animation: outline-btn-play-breathing 1s infinite alternate;
            height: .05px;
            width: .05px;
            background-color: rgb(245, 242, 207);
            border-radius: 50%;
        }

        &-play-btn{
            position: relative;
            width: 300px;

            img{
                width: 100%;
                height: auto;
            }

            .wrapper{
                transform: scale(0.1);
                position: absolute;
                transition: all 0.5s ease-in-out;

                &--left{
                    top: 20px;
                    left: 42px;
                }

                &--right{
                    top: 45px;
                    right: 34px;
                }
            }

            &:hover .wrapper{
                transform: scale(0.6);
            }
        }
    }