.partner{

            margin-top: 100px;

            &-title{
                text-align: center;
                font-style: italic;
                font-size: 60px;
                font-weight: 800;
                background-image: linear-gradient(180deg, #FDE426 10%, #C07320 90%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;

                span{
                    font-size: 20px;
                    color: #FDE426;
                    display: block;
                }
            }

            &-logo{
                margin-top: 100px;

                .row{
                    justify-content: center;
                }

                &-img{
                    margin-bottom: 30px;
                    transition: all 0.3s ease-in-out;

                    &:hover{
                        cursor: pointer;
                        transform: scale(0.98);
                        img{
                            filter: drop-shadow(0 0 10px #E9C124);
                        }
                    }

                    img{
                        width: 100%;
                        height: auto;
                    }
                }
            }
        }