.buy{
    position: relative;

    &-loading-screen{
        position: absolute;
        height: 100vh;
        width: 100vw;
        top: 0;
        left: 0;
        background-color: rgba(255, 255, 255, 0.489);
        z-index: 10;
    }

    .link-bg{
        position: absolute;
        top: -130px;
        left: 0;
        width: 100%;

        img{
            width: 100%;
            height: auto;
        }
    }

    .title{
        position: absolute;
        top: 50px;
        left: 50%;
        transform: translateX(-50%);
    }

    &-paragraph{
        padding: 220px 0 50px;
        color: #fff;
        font-weight: 700;
        font-size: 20px;
        // text-align: justify;
        text-align: center;
        font-style: italic;

        .col-xl-12{
            padding: 0 100px;
            text-shadow: 2px 0 0 #000, -2px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000;
        }
    }

    &-form{
        height: 400px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 20px;

        &-content-error-message{
            position: absolute;
            right: 0;
            bottom: 0;
            transform: translateY(40px);
            color: red;
            font-size: 16px;
            font-weight: 600;
        }
        
        &-wrap{
            position: relative;
            // width: 600px;
            height: 100%;
        }
        
        &-bg{
            height: 100%;
            width: auto;
            
            img{
                height: 100%;
                width: auto;
            }
        }

        &-tree{
            position: absolute;
            bottom: 0;
            left: -18%;
            
            img{
                height: 400px;
            }
            z-index: 1;
        }

        &-balance{
            position: absolute;
            box-sizing: border-box;
            top: -5%;
            left: 20%;
            // width:  400px;
            min-width: 140px;
            color: #F5EA6B;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 28px;
            font-weight: 900;
            padding: 40px 40px;
            background-position: center center;
            background-size: contain;
            background-repeat: no-repeat;
            transition: all 0.3s ease-in-out;
            background-image: url(../../../public/images/khung1.png);
        }

        &-buy-btn{
            position: absolute;
            bottom: 11px;
            right: 19%;
            height: 150px;
            width: 214px;
            min-width: 140px;
            color: #F5EA6B;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 24px;
            font-weight: 900;
            padding: 20px 10px;
            background-position: center center;
            background-size: contain;
            background-repeat: no-repeat;
            transition: all 0.3s ease-in-out;
            background-image: url(../../../public/images/bg-marketplace-btn.png);
            animation: btn-breathing 1s infinite alternate;
            opacity: 0.9;

            &:hover{
                transform: scale(0.9);
                opacity: 1;
                cursor: pointer;
            }
        }

        &-content{
            position: absolute;
            top: 47%;
            left: 50%;
            transform: translate(-50%,-50%)
        }

        &-content-row{
            display: flex;
            margin-left: 165px;
        }

        &-content-row.first{
            margin-bottom: 20px;
        }

        &-content-title{
            font-size: 14px;
            font-weight: 800;
            display: flex;
            align-items: center;
            
            &--first{
                min-width: 80px;
                justify-content: flex-end;
                margin-right: 8px;
            }

            &--second{
                margin-left: 8px;
            }
        }

        &-content-input{
            border: 2px solid #BC6220;
            border-radius: 8px;
            height: 40px;
            background-color: transparent;
            font-weight: 500;
            font-style: italic;
        }
    }
}