@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import '../src/scss/animations';
@import '../src/scss/responsive';


.App {
  background-color: transparent;
  min-height: 100vh;
  width: 100vw;
  overflow-y: auto;
  overflow-x: hidden;

  font-family: 'Montserrat', sans-serif;

  &::-webkit-scrollbar {
    display: none;
  }

  &.loading{
    height: 100vh !important;
  }

  // fonts
  @import url('https://fonts.googleapis.com/css2?family=Chewy&display=swap');
}

.empty {
  height: 500px;
}

html {
  width: 100vw;
  overflow-x: hidden;
  scroll-behavior: smooth;
}


.ytp-impression-link {
  display: none;
}

.header-mobile-wrap {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  background-color: black;
}

.header-mobile {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  padding: 10px 0;

  &-logo {
    height: 50px;

    img {
      height: 100%;
      width: auto;
    }
  }

  &-icon {
    i {
      font-size: 30px;
      color: green;
    }
  }

}

.nav-logo {
  height: 36px;

  img {
    height: 100%;
  }
}

.nav-item {
  height: 60px;
  border-bottom: 1px solid rgba(221, 221, 221, 0.353);
  width: 100%;
  display: flex;
  align-items: center;
  // font-size: 60px;

  &-icon {
    font-size: 20px;
    color: green;
    margin-right: 8px;
    margin-left: 4px;
    min-width: 30px;

    img {
      height: 100%;
      width: auto !important;
    }
  }

  &-name {
    font-size: 18px;
    font-weight: 500;
  }
}

.cart-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  height: 70px;
  border-bottom: 1px solid #ddd;
  background-color: #000;

  .nav-logo {
    height: 80%;

    img {
      width: auto !important;
    }
  }

  &-close {
    .clear-btn {
      padding: 0;
    }

    i {
      font-size: 24px;
      color: #000;
    }
  }
}

.nav-item-name {
  color: #fff;
  padding: 0 20px;
  font-weight: 700;

  &.active {
    font-size: 20px;
    font-weight: 900;
    background-image: linear-gradient(180deg, #FDE426 10%, #C07320 90%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &--primary {
    background-image: linear-gradient(180deg, #FDE426 10%, #C07320 90%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

.clear-btn {
  background-color: unset;
  border: none;
}

.MuiModal-root {
  & div:nth-child(3) {
    background-image: linear-gradient(180deg, #3F1927 10%, #1A1C3B 90%) !important;
  }
}

.css-1hskriy {
  width: 300px !important;
}