
// clear default element
input{
    outline:none;
    padding-left: 4px;
}

a{
  display: block;
  color: unset;
  text-decoration: none;

  &:hover{
    text-decoration: none;
  }
}

// custom class inline

.mt-90{
    margin-top: 90px;
}


.text-gradient{
    // background-image: linear-gradient(180deg, #D70913 10%, #810A0D 90%);
    // -webkit-background-clip: text;
    // -webkit-text-fill-color: transparent;
    // -webkit-text-stroke-width: 1px;
    // -webkit-text-stroke-color: rgba(255, 255, 255, 0.58);
    color: #B20102;
    text-shadow: 2px 0 0 #fff, -2px 0 0 #fff, 0 2px 0 #fff, 0 -2px 0 #fff, 1px 1px #fff, -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff;
}

.text-border{
    -webkit-text-stroke: 1px rgba(96, 83, 83, 0.778);
}

.title{
    display: flex;
    justify-content: center;

    &-bg{
        height: 200px;

        img{
            height: 100%;
            width: auto;
        }
    }

    &-content{
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        padding-top: 45px;
        font-size: 28px;
        font-weight: 900;
    }
}

.paragraph{
    padding: 10px 0 50px;
    color: #fff;
    font-weight: 700;
    font-size: 20px;
    text-align: justify;
    font-style: italic;

    .col-xl-12{
        padding: 0 100px;
        text-shadow: 2px 0 0 #000, -2px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000;
    }
}

.fire {
    font-size: 18px;
    filter: blur(0.02em);
    -webkit-filter: blur(0.02em);
    margin: 3em auto 0 auto;
    width: 7em;
    height: 5em;
    transition-duration: 200ms;
    transition-timing-function: ease-out;
    position: absolute;
    transform: translate(-50%, -100%);
    top:30%;left:51%;margin:auto;
}

.show > .particle {
    animation: rise 1s ease-in infinite;
    background-image: radial-gradient(#ff5000 20%, rgba(255, 80, 0, 0) 70%);
    border-radius: 50%;
    mix-blend-mode: screen;
    opacity: 0;
    position: absolute;
    bottom: 0;
    width: 5em;
    height: 5em;
}
.particle:nth-of-type(1) {
    animation-delay: 0.7667885121s;
    left: calc((100% - 5em) * 0);
}
