.road-map{
        margin-top: 300px;

        .title{
            transform: translateY(-45px);
        }

        &-boss{
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.5s ease-in-out;

            img{
                width: 500px;
                height: auto;
            }
        }

        &-wrap{
            display: flex;
        }

        &-left,
        &-right{
            width: 40%;
        }

        &-middle{
            width: 20%;
        }

        &-left{
            display: flex;
            flex-direction: column;
            align-items: flex-end;
        }

        // LAND
        &-land{
            position: relative;
            width: 333px;
            z-index: 2;
            transition: all 0.5s ease-in-out;
            animation: land-up-and-down 1s infinite alternate;
            position: relative;

            &:hover{
                cursor: pointer;

                img{
                    filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.638));
                }
            }

            &-title{
                position: absolute;
                bottom: 20%;
                left: 0;
                height: 50px;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                img{
                    height: 100% !important;
                    width: auto !important;    
                }
            }

            img{
                width: 100%;
                height: auto;
                transition: all 0.5s ease-in-out;
            }

            &--3{
                animation-delay: .6s;
            }
            &--4{
                animation-delay: .4s;
            }
            &--5{
                animation-delay: .5s;
            }
            &--6{
                animation-delay: .3s;
            }

            &--2{
                margin-top: 164px;
                z-index: 2;
                animation-delay: .2s;
            }
        }

        // BRIDGE
        &-bridge{
            width: 100%;
            position: relative;
            animation: land-up-and-down 1s infinite alternate;

            img{
                width: 100%;
                height: auto;
            }

            &--1{
                margin-top: 288px;

                img{
                    transform: scale(2.2) rotate(1deg);
                }
            }

            &--2{
                margin-top: 125px;

                img{
                    transform: scale(2.2) rotate(1deg);
                }
            }

            &--3{
                margin-top: 125px;

                img{
                    transform: scale(2.2) rotate(1deg);
                }
            }

            &--4{
                margin-top: 186px;

                img{
                    transform: scale(2.2) rotate(1deg);
                }
            }

            &--2{
                animation-delay: .2s;
            }

            &--3{
                animation-delay: .6s;
            }
            &--4{
                animation-delay: .4s;
            }
            &--5{
                animation-delay: .5s;
            }
            &--6{
                animation-delay: .3s;
            }
        }

        .final-boss{
            height: 300px;
            display: flex;
            justify-content: flex-end;
            transform: translateY(-127px);

            img{
                height: 100%;
                width: auto;
                margin-right: 20%;
            }
        }

        &-modal{
            position: fixed;
            top: 0;
            left: 0;
            width: 100vw;
            height: 100vh;
            background: rgba(28, 28, 28, 0.19);
            backdrop-filter: blur(2px);
            z-index: 4;
            display: flex;
            
            &-bg{
                margin: auto;
                width: 800px;

                img{
                    width: 100%;
                    height: auto;
                }
                position: relative;
                animation: modal-road-map-open 1s alternate;
            }

            &-close-btn{
                position: absolute;
                top: 5%;
                right: 0%;
                height: 100px !important;
                width: 100px !important;
                z-index: 4;
                transition: all 0.5s ease-in-out;

                &:hover{
                    cursor: pointer;
                    transform: scale(.9);
                }
            }

            &-content{
                position: absolute;
                top: 0%;
                left: 0;
                height: 100%;
                width: 100%;
                padding: 18% 10% 10%;
                color: #F3CA51;

                &-wrap{
                    height: 100%;
                    width: 100%;
                    overflow-y: scroll;
                    
                    &::-webkit-scrollbar {
                        -webkit-appearance: none;
                        width: 5px;
                    }

                    &::-webkit-scrollbar-thumb {
                        border-radius: 2.5px;
                        background-image: linear-gradient(180deg, #c4aa03 10%, #EA991A 90%);
                        -webkit-box-shadow: 0 0 1px rgba(255,255,255,.5);
                    }
                }

                &-item{
                    display: flex;
                    align-items: center;
                }

                &-item:not(:last-child){
                    margin-bottom: 20px;
                }

                &-item .icon{
                    height: 30px;
                    width: 30px;
                    margin-right: 12px;
                }

                &-item .titles{
                    font-weight: 700;
                    font-size: 20px;
                    background-image: linear-gradient(180deg, #FDE426 10%, #C07320 90%);
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                }
            }
        }
    }