@import '../../scss/_animations.scss';

.modal__bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(28, 28, 28, 0.19);
    backdrop-filter: blur(6px);
    opacity: 1;
    animation-timing-function: ease-out;
    animation-duration: 0.3s;
    animation-name: modal-video;
    -webkit-transition: opacity 0.3s ease-out;
    -moz-transition: opacity 0.3s ease-out;
    -ms-transition: opacity 0.3s ease-out;
    -o-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
    z-index: 100;

    iframe{
        height: 500px;
        width: 700px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        border-radius:20px;
    }
}



.video-wrap{
    display: flex;
    align-items: center;
    justify-content: center;
    position:absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);

    &-border{

        img{
            height: 582px;
            width: 760px;
        }
    }
}

.video-close-btn{
    position: absolute;
    top: 0;
    right: 0;
    z-index: 3;
    height: 50px;
    transition: all 0.5s ease-in-out;

    &:hover{
        cursor: pointer;
        transform: scale(1.2);
    }
}

.modal__spinner{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    
    img{
        transition: opacity 0.3s ease-out;
        animation: planet-rotate 1s infinite linear;
    }
}



.video-wrap{
    transition: all 0.5s ease-in-out;
    animation: video-frame-open 1s forwards;
}