@import '../../scss/variables';

.home-header{
    z-index: 1;
    background-color: transparent;
    padding-top: 10px;
    height: 100px;

    &-logo{
        margin-top: 20px;
        height: 100%;
        img{
            height: auto;
            width: 130%;
        }        
    }

    &-nav{
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        color: $white;

        &-bg{
            img{
                width: 100%;
                height:auto;
            }
        }

        &-content{
            position: absolute;
            top: 53px;
            display: flex;
            padding: 0 20px;
        }
    }

    &-item{
        font-weight: 700;
        font-size: 20px;
        font-style: italic;
        // min-width: 150px;
        // width: 20%;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 80%;
        transition: all 0.2s ease-in-out;
        color: #efc65a;
        margin-right: 35px;

        &:hover{
            cursor: pointer;
            color: #fff;
        }
    }

    &-btns{
        height: 100%;
        width: 100%;
        margin-top: 20px;
        margin-left: 20px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
    }

    &-btn{
        position: relative;
        height: 50px;
        transition: all 0.2s ease-in-out;

        a{
            height: 100%;
        }
        
        img{
            height: 100%;
            width: auto;
        }

        &:hover{
            cursor: pointer;
            transform: scale(0.9);
        }
    }

    &-btn:first-child{
        margin-bottom: 8px;
    }

}