@import '../../scss/_animations.scss';

.background-outro{
    background-color: black;
    position: relative;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    z-index: -1;

    &-link-bg{
        position: absolute;
        top: -99px;
        height: 200px;
        width: 100%;
        z-index: 1;

        img{
            height: 100%;
            width: 100%;
        }
    }

    &-image{
        position: absolute;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;

        &-wrap{
            position: relative;
            height: 100%;
        }

        img{
            height: 100%;
            width: auto;
        }
    }

    $shooting-time: 5000ms;

    .night {
        position: absolute;
        top: -10%;
        left: 20%;
        width: 500px;
        height: 100%;
        transform: rotateZ(29deg);
    }

    .shooting_star {
        position: absolute;
        left: 50%;
        top: 50%;
        // width: 100px;
        height: 2px;
        background: linear-gradient(-45deg, rgba(95, 145, 255, 1), rgba(0, 0, 255, 0));
        border-radius: 999px;
        filter: drop-shadow(0 0 6px rgba(105, 155, 255, 1));
        animation:
            tail $shooting-time ease-in-out infinite,
            shooting $shooting-time ease-in-out infinite;
        
        &::before {
            content: '';
            position: absolute;
            top: calc(50% - 1px);
            right: 0;
            // width: 30px;
            height: 2px;
            background: linear-gradient(-45deg, rgba(0, 0, 255, 0), rgba(95, 145, 255, 1), rgba(0, 0, 255, 0));
            transform: translateX(50%) rotateZ(45deg);
            border-radius: 100%;
            animation: shining $shooting-time ease-in-out infinite;
        }

        &::after {
            content: '';
            position: absolute;
            top: calc(50% - 1px);
            right: 0;
            // width: 30px;
            height: 2px;
            background: linear-gradient(-45deg, rgba(0, 0, 255, 0), rgba(95, 145, 255, 1), rgba(0, 0, 255, 0));
            transform: translateX(50%) rotateZ(45deg);
            border-radius: 100%;
            animation: shining $shooting-time ease-in-out infinite;
            transform: translateX(50%) rotateZ(-45deg);
        }
    
        @for $i from 1 through 20 {
            &:nth-child(#{$i}) {
                $delay: random(9999) + 0ms;
                top: calc(50% - #{random(400) - 200px});
                left: calc(50% - #{random(300) + 0px});
                animation-delay: $delay;
                // opacity: random(50) / 100 + 0.5;
                
                &::before,
                &::after {
                    animation-delay: $delay;
                }
            }
        }
    }

    .planet{
        position: absolute;
        transition: all 30s ease-in-out;

        img{
            height: 100%;
            width: auto;
        }

        &--1{
            top: 10%;
            right: 24%;
            height: 24%;
            animation: planet-rotate 60s infinite linear;

            img{
                animation: planet-light-1 2s infinite alternate-reverse;
            }
        }

        &--2{
            top: 28%;
            right: 34%;
            height: 13%;
            animation: planet-rotate 45s infinite linear;
            animation-direction: reverse;

            img{
                animation: planet-light-2 2s infinite alternate;
            }
        }

        &--3{
            top: 13%;
            right: 44%;
            height: 28%;
            animation: planet-rotate 90s infinite linear;

            img{
                animation: planet-light-1 2s infinite alternate;
            }
        }

        &--4{
            top: 28%;
            right: 58%;
            height: 15%;
            animation: planet-rotate 70s infinite linear;
            animation-direction: reverse;

            img{
                animation: planet-light-4 2s infinite alternate;
            }
        }
    }

    .start{
        position: absolute;
        transform: translateX(-50%);
        animation: start-breathing 1s infinite alternate;
        height: 1px;
        width: 1px;
        background-color: rgb(207, 230, 245);
        border-radius: 50%;

        &--1{
            bottom: 41%;
            left: 52%;
        }
    }

    .infor-contact{
        position: absolute;
        bottom: 20px;
        left: 0;
        text-align: center;
        width: 100%;
        color: #fff;
        font-weight: 600;
        font-style: italic;
        text-shadow: 2px 0 0 #000, -2px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000;
    }

}