.loading-screen{
    position: absolute;
    height: 100vh;
    width: 100vw;
    background-color: #fff;
    z-index: 10;
    background: #202020;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;

    &-wrapper{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    &-logo{
        height: 100px;
        width: auto;
        margin-bottom: 10px;
        animation : logo-loading-screen 1s infinite alternate;
    }

    .shadow{
        height: 5px;
        width: 40px;
        background: #000;
        border-radius: 50%;
        bottom: 0;
        animation: shadow 1s infinite;
    }

    h1 {
        margin-top: 30px;
        height: 40px;
        text-transform: uppercase;
        text-align: left;
        text-shadow: 0 0 20px #B20102;
        letter-spacing: 0.1em;
        font-size: 14px;
        font-weight: lighter;
        color: #B20102;
        display: flex;
        justify-content: center;

        span {
            display: none;
        }
        &::after {
            animation: txt 1s infinite alternate;
            color: #B20102;
            font-weight: 900;
            font-size: 14px;
            text-shadow: 2px 0 0 #fff, -2px 0 0 #fff, 0 2px 0 #fff, 0 -2px 0 #fff, 1px 1px #fff, -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff;
            content: "";
        }
    }
}

